.techin-cta{
    &-section{
        position: relative;
        z-index: 1;
        padding: 110px 0;
        max-width: 90%;
        border-radius: 20px;
        margin-bottom: -180px;
        margin: 0 auto;
        background-position: center;
        background-size: cover;
        @include max (1199px){
            max-width: 85%;
        }
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
        @include max (575px){
            padding: 45px 0;
        }
    }
    &-content {
        
        &-top{
            display: flex;
            gap: 7px;
            margin-bottom: 10px;
            h6{
                color: var(--white-color);
            }
            @include max (991px){
                justify-content: center;
            }
        }
        &-bottom{
            h2{
                color: var(--white-color);
            }
            @include max (991px){
                text-align: center;
            }
        }
    }
}
.techin-title-btn {
    @include max (991px){
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
}