
header.techin-header-section {
  position: fixed;
  z-index: 999;
  width: 100%;
  top: 0;
  transition: all 0.4s;
  background: var(--light-color);
  padding: 15px 0;
}
header.techin-header-section.sticky-menu{
  box-shadow: 0 4px 80px rgba(0,0,0,.1);
  padding: 30px 0 15px 0;
  @include max (1199px){
    padding: 15px 0 15px 0;
  }
}
.sticky-menu .techin-header-top{
  margin-top: -57px;
  transition: all 0.4s;
  opacity: 0;
}
.sticky-menu .header-logo{
  transition: all 0.4s;
  margin-top: -20px;
  @include max (1199px){
    margin-top: 0;
  }
  img{
    transition: all 0.4s;
    padding: 0;
  }
}

.techin-header{
  &-top{
    transition: all 0.4s;
    @include max(1199px) {
      display: none;
    }
  }
}

.header-logo{
  transition: all 0.4s;
  max-width: 280px;
  flex-shrink: 0;
  @include max (1365px){
    max-width: 190px;
  }
  @include max (575px){
    max-width: 150px;
  }
  img{
    transition: all 0.4s;
    padding: 35px 0;
    @include max (1199px){
      padding: 0;
    }
  }
}
// end main
.techin-header-bottom{
  transition: all 0.4s;
  .techin-header{
    display: flex;
    justify-content: space-between;
  }
  .techin-header-bottom2{
    padding: 0 18px;
    border-radius: 10px;
    background-color: var(--white-bg);
    @include max(991px) {
      padding: 12px;
    }
  }
}

.techin-header-main-menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 20px;
  padding-left: 40px;
  padding-right: 20px;
  @include max (1350px){
    padding-left: 24px;
  }
  @include max (1199px){
    justify-content: end;
    background: none;
  }
  
}
.techin-header-one.sticky-menu .techin-header-bottom{
  @include max(991px) {
    padding: 0;
  }
}

