.techin-header-triger a {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-radius: 100px;
}


.techin-header-icon {
  display: flex;
  align-items: center;
  gap: 20px;
  position: relative;
  margin: 0 30px;
  @include max(1199px){
    display: none;
  }
 
}

a.techin-header-info{
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  color: var(--dark-bg);
  padding: 12px 0;
  img{
    padding-right: 10px;
  }
}

.techin-header-barger{
  cursor: pointer;
  position: relative;
  width: 52px;
  height: 52px;
  display: flex;
  border-radius: 6px;
  align-items: center;
  justify-content: center;
  background-color: #1E2656;
  &.light-color{
    background-color: rgba(255, 255, 255, 0.12);
  }
  @include max(767px) {
    display: none;
  }
  span {
    height: 2px;
    width: 30px;
    margin: 4px 0;
    display: block;
    position: relative;
    background-color: var(--white-bg);
    &:before {
      content: "";
      top: -8px;
      width: 15px;
      height: 2px;
      transition: all 0.4s;
      position: absolute;
      transform: translateX(10px);
      background-color: var(--white-bg);
    }
    
    &::after {
      content: "";
      bottom: -8px;
      width: 15px;
      height: 2px;
      position: absolute;
      transition: all 0.4s;
      transform: translateX(6px);
      background-color: var(--white-bg);
    }
    
  }


  &:hover span:before{
    transform: translateX(5px);
  }
  &:hover span::after{
    transform: translateX(10px);
  }
  
}

// end v3

.sticky-menu.header-four{
  .techin-header-bottom {
    background-color: var(--white-bg);
  }
}

// start search

.techin-header-search {
  cursor: pointer;
  z-index: 1;
  position: relative;
  color: var(--accent-color);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  background: var(--white-bg);
  border-radius: 100px;
  i{
    font-size: 24px;
    line-height: 24;
    font-weight: 900;
    
  }
  &.search-box{
    width: 40px;
    height: 40px;
    background: var(--accent-bg);
    i{
      font-size: 20px;
      font-weight: 500;
      color: var(--white-color);
    }
  }
  
  @include max(767px) {
    display: none;
  }
  &-section{
    opacity: 0;
    visibility: hidden;
    position: fixed;
    width: 100%;
    background-color: #fff;
    top: 0;
    z-index: 9999;
    padding: 130px 0 80px;
    transition: 0.5s all ease;
    transform: translateY(-100%);
    @include max(991px) {
      padding: 100px 0 80px;
    }
    @include max(767px) {
      padding: 80px 0;
    }
    &.open{
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }
  &-close{
    position: absolute;
    top: 40px;
    right: 40px;
    cursor: pointer;
    i{
      font-size: 30px;
      color: #6E6E77;
      transition: all 0.4s;
    }
    &:hover i{
      color: var(--accent-color);
    }
  }
}

.search-overlay.open {
  opacity: .8;
  z-index: 3;
  visibility: visible;
}
.search-overlay {
  top: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 0;
  visibility: hidden;
  opacity: 0;
  transition: .5s;
  background-color: rgba(4, 13, 67, 0.5);
}
