// barger icon

.site-header .techin-menu-toggle {
  display: flex;
  height: 30px;
  width: 30px;
  margin-left: 40px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
}
.site-header .techin-menu-toggle span {
  display: block;
  height: 3px;
  background-color: var(--heading-color);
  width: 30px;
  position: relative;
}
.reveal-header .techin-menu-toggle span {
  background-color: var(--heading-color) !important;
}
.site-header .techin-menu-toggle span:before,
.site-header .techin-menu-toggle span:after {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--heading-color);
}
.reveal-header .techin-menu-toggle span:before,
.reveal-header .techin-menu-toggle span:after {
  background-color: var(--heading-color) !important;
}
.dark-mode-texts .techin-menu-toggle span {
  background-color: var(--heading-color);
}
.dark-mode-texts .techin-menu-toggle span:before,
.dark-mode-texts .techin-menu-toggle span:after {
  background-color: var(--heading-color);
}
.site-header .techin-menu-toggle span:before {
  top: -10px;
}
.site-header .techin-menu-toggle span:after {
  top: 9px;
}

.site-header .mobile-menu-trigger.light-color span {
  background-color: var(--white-color);
}

.site-header .mobile-menu-trigger.light-color span::after, 
.site-header .mobile-menu-trigger.light-color span::before {
  background-color: var(--white-color);
}

.site-header .mobile-menu-trigger.light-color span::after, 
.site-header .mobile-menu-trigger.light-color span::before {
  background-color: var(--white-color);
}







// End Header


.techin-header{
  &-info{
    position: relative;
    &::before{
      content: "";
      top: 2px;
      left: 27%;
      width: 1px;
      height: 20px;
      position: absolute;
      border-right: 1px solid var(--body-color);
    }
    &::after{
      content: "";
      top: 2px;
      left: 60%;
      width: 1px;
      height: 20px;
      position: absolute;
      border-right: 1px solid var(--body-color);
    }
    &-wrap{
      display: flex;
      align-items: center;
      padding: 0 0 10px 0;
      justify-content: space-between;
    }
    ul{
      li{
        display: inline-block;
        position: relative;
        margin-right: 40px;
        &:last-child{
          margin-right: 0;
        }
        a{
          color: var(--body-color);
          img{
            padding-right: 5px;
          }
        }
        i{
          left: 0;
          font-size: 16px;
          position: absolute;
          color: var(--accent-color);
        }
      }
    }
  }
  &-social{
    ul{
      li{
        display: inline-block;
        margin-left: 10px;
        &:first-child{
          margin-left: 0;
        }
          a{
            width: 30px;
            height: 30px;
            display: flex;
            transition: all 0.4s;
            align-items: center;
            border-radius: 100px;
            justify-content: center;
            background: var(--accent-bg2);
            &:hover{
              background: var(--accent-bg);
            }
            i{
              color: var(--white-color);
            }
          }
      }
    }
  }
}

