
.techin-footer{
  &-section{
    margin-top: -155px;
    padding-top: 270px;
    @include max (991px){
      padding-top: 240px;
      margin-top: -180px;
    }
    @include max (767px){
      padding-top: 190px;
      margin-top: -150px;
    }
  }
  &-top{
    padding-bottom: 60px;
    @include max (991px){
      padding-bottom: 40px;
    }
    @include max (767px){
      padding-bottom: 30px;
    }
  }
  &-textarea{
    @include max(1199px) {
      max-width: 400px;
      margin-bottom: 40px;
    }
    img{
      margin-bottom: 30px;
    }
    p{
      line-height: 30px;
      margin-bottom: 28px;
      color: var(--light-color2);
    }
    &.dark-color{
      p{
        color: var(--body-color);
      }
    }
  }
  &-title{
    position: relative;
    &::before{
      content: "";
      position: absolute;
      bottom: -8px;
      left: 0;
      width: 52px;
      height: 4px;
      border-radius: 2px;
      background: var(--accent-bg);
    }
    h5{
      margin-bottom: 45px;
      color: var(--white-color);
      @include max(767px) {
      margin-bottom: 15px;
      }
    }
    p{
      color: var(--light-color2);
    }
    &.dark-color{
      h5{
        color: var(--heading-color);
      }
      p{
        color: var(--body-color);
      }
    }
  }
  &-menu{
    @include max(767px) {
      margin-bottom: 35px;
    }
    ul{
      li{
        margin-bottom: 8px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          color: var(--white-color);
          svg{
            margin-right: 5px;
          }
          &:hover{
            color: var(--accent-color2);
            path{
              fill: var(--accent-bg2);
            }
          }
          &:before{
            content: "";
            position: absolute;
            bottom: -3px;
            left: auto;
            right: 0;
            height: 1px;
            width: 0;
            background-color: var(--accent-bg2);
            transition: all .3s ease;
          }
          &:hover:before{
            left: 0;
            right: auto;
            width: 100%;
          }
        }
      }
    }
    &.dark-color{
      ul{
        li{
    
          a{
            color: var(--body-color);
            &:hover{
              color: var(--heading-color);
            }
            &:before{
              background: var(--heading-color);
            }
         
          }
        }
      }
    }
  }
  &-info{
    padding: 20px 0;
    background: linear-gradient(90deg, #1B1885 0%, #2F2BEB 44%, #1B1885 100%);
    &-wraper{
      display: flex;
      align-items: center;
      gap: 144px;
      position: relative;
      flex-wrap: wrap;
      @include max (1399px){
        gap: 66px;
      }
      @include max (1199px){
        gap: 40px;
      }
      @include max (767px){
        gap: 30px;
      }
      &::before{
        position: absolute;
        content: "";
        top: 0;
        left: 37%;
        width: 1px;
        height: 60px;
        border-right: 1px solid var(--heading-color);
        @include max (1399px){
          display: none;
        }
      }
      &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 69%;
        width: 1px;
        height: 60px;
        border-right: 1px solid var(--heading-color);
        @include max (1399px){
          display: none;
        }
      }
    }
    &-item{
      display: flex;
      gap: 20px;
      align-items: center;
    }
    &-text{
      ul{
        li{
          a{
            span{
              font-size: 16px;
              font-weight: 700;
              line-height: 26px;
              color: var(--white-color);
            }
            h5{
              color: var(--white-color);
            }
          }
        }
      }
    }
  }
  &-bottom{
    padding: 30px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include max(991px) {
      padding: 25px 0;
      text-align: center;
    }
    @include max(767px) {
      display: block;
      text-align: center;
    }
    &.center{
      text-align: center;
    }
    &.one{
      border-top: 1px solid var(--light-color1);
    }
    .techin-copywright{
      p{
       color: var(--white-color);
       font-weight: 700;
       font-family: $heading-font;
       span.footer-text{
         color: var(--accent-color2);
       }
       @include max(767px) {
        margin-bottom: 15px;
      }
      }
      &.dark-color{
       p{
         color: var(--body-color);
       }
     }
   }
  }
}



.ml30{
  margin-left: 30px;
  @include max(767px) {
    margin-left: 0;
  }
}

// start v5
.footer-v5{
  z-index: 0;
  position: relative;
  &::before{
    content: "";
    left: 0;
    bottom: 0;
    z-index: -1;
    width: 100%;
    height: 84%;
    position: absolute;
    background-color: var(--dark-bg);
    @include max(767px) {
      height: 92%;
    }
  }
  &::after{
    content: "";
    left: 0;
    bottom: 0;
    z-index: -2;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: var(--light-bg1);

  }
}

.techin-footer{
  &-form{
    padding: 80px;
    position: relative;
    @include max(991px) {
      padding: 60px;
    }
    @include max(767px) {
      padding: 40px;
    }
    h5{
      margin-bottom: 6px;
    }
    &-wrap{
      position: relative;
      background-color: var(--white-bg);
      border-radius: 90px 30px 90px 30px;
      &::before{
        content: "";
        left: 0;
        top: 0;
        width: 0;
        height: 0;
        position: absolute;
        border-top: 70px solid var(--light-bg1);
        border-right: 70px solid transparent;
      }
      &::after{
        content: "";
        right: 0;
        bottom: 0;
        width: 0;
        height: 0;
        position: absolute;
        border-bottom: 70px solid var(--dark-bg);
        border-left: 70px solid transparent;
      }
      @include max(991px) {
        border-radius: 70px 25px 70px 25px;
        &::before{
          border-top: 60px solid var(--light-bg1);
          border-right: 60px solid transparent;
        }
        &::after{
          border-bottom: 60px solid var(--dark-bg);
          border-left: 60px solid transparent;
        }
        
      }
    }
  }
  
}

#map{
  height: 100%;
  width: 100%;
  border-radius: 0px 30px 90px 0px;
  @include max(991px) {
    height: 500px;
    border-radius: 0px 0px 70px 0px;
  }
  @include max(767px) {
    height: 400px;
  }
}


// End v5
